.header {
  position: fixed;
  top: 0;
  width: 90%;
  z-index: 999;
  padding: 16px 0 16px 85px;
  background: #FEFEFE;
  border-bottom: 1px solid #e6e6e6;
}
.header .logo {
  width: 177px;
}
.header .mobNav {
  display: none;
}

@media screen and (max-width: 1400px) {
  .header {
    padding: 16px 0 16px 55px;
  }
}
@media screen and (max-width: 1250px) {
  .header {
    width: 92%;
    display: flex;
    justify-content: space-between;
    padding: 10px 5px;
  }
  .header .logo {
    width: 110px;
  }
  .header .mobNav {
    display: block;
    position: absolute;
    height: 100vh;
    z-index: 1000;
    background-color: #ffff;
    width: 100%;
    top: calc(0% + 62px);
    left: 0;
  }
}