@import "../../../../colors.scss";

.lecturer {
  margin-bottom: 15px;
  padding: 0 50px;
  .filter {
    display: flex;
    // justify-content: space-between;
    margin-bottom: 25px;
    animation: fadein 0.6s;
    select {
      border: none;
      border-bottom: 1px solid $black;
      font-size: 1em;
      background-color: inherit;
      margin-right: 16px;
    }
    .department {
      width: 235px;
    }
    input[type="search"] {
      border: none;
      background-color: inherit;
      border-bottom: 1px solid $black;
      font-size: 1em;
      width: 185px;
      padding: 1px 5px;
      &::placeholder {
        color: $black;
        // text-align: center;
      }
    }
  }
}
.day {
  margin-bottom: 20px;
  animation: fadein 0.8s;
  .header {
    display: flex;
    justify-content: space-between;
    font-size: 19px;
    font-weight: 500;
    margin-bottom: 10px;
    .weekday {
      text-transform: capitalize;
    }
  }
  .lecturer {
    padding: 0;
    text-align: left;
    font-weight: 500;
    margin-bottom: 15px;
    color: $dark-blue;
  }
  .lesson {
    text-align: left;
    display: flex;
    font-size: 17px;
    padding-bottom: 10px;
    margin-bottom: 12px;
    border-bottom: 1px solid #e9e9e9;
    &:last-child {
      border-bottom: none;
      padding-bottom: 2px;
    }
    .start {
      padding-left: 10px;
      width: 26px;
      font-weight: 400;
      color: darken($gray, 5);
    }
    .groupAndAudience {
      text-align: left;
      .group {
        margin-right: 5px;
      }
    }
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 1250px) {
  .lecturer {
    padding: 0 5px;
    .filter {
      flex-direction: column;
      align-items: center;
      select {
        background-color: inherit;
        width: 100%;
        margin: 0;
        margin-bottom: 10px;
        padding: 2px 0;
      }
      .department {
        width: 100%;
      }
      input[type="search"] {
        background-color: inherit;
        width: 100%;
        padding: 5px;
        &::placeholder {
          text-align: left;
        }
      }
    }
    .day {
      .header {
        font-size: 1.05em;
        span:nth-child(2) {
          font-size: 0.95em;
        }
      }
      .lesson {
        .groupAndAudience {
          font-size: 0.95em;
          .group:first-child {
            display: block;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
}
