.tab {
  width: 100%;
  padding: 25px;
  font-family: sans-serif;
}

.inline {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
}
.inline li {
  width: 100%;
  text-align: center;
  margin-left: 0;
  padding: 10px 7px;
  transition: all 0.1s;
  font-weight: 500;
  cursor: pointer;
  background-color: #ededed;
}
.inline li.selected {
  background-color: #fcfcfc;
  color: #5581F1;
}

.panel {
  padding: 32px 0;
  text-align: center;
  background-color: #fcfcfc;
}

@media screen and (max-width: 1250px) {
  .inline li {
    font-size: 0.88em;
  }

  .panel {
    padding-top: 22px;
  }
}