@import "../../../../colors.scss";

.students {
  text-align: center;
  padding: 0 50px;
  .filter {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    animation: fadein 0.6s;
    .radios {
      .radio {
        margin-right: 10px;
        label {
          margin-right: 3px;
        }
      }
    }
    select {
      border: none;
      border-bottom: 1px solid $black;
      font-size: 1em;
      background-color: inherit;
    }
    #specialty {
      width: 210px;
    }
    input[type="search"] {
      border: none;
      background-color: inherit;
      border-bottom: 1px solid $black;
      font-size: 1em;
      width: 197px;
      padding: 0 5px;
      &::placeholder {
        color: $black;
        text-align: center;
      }
    }
  }
  .schedule {
    .day {
      margin-bottom: 20px;
      animation: fadein 0.8s;
      .header {
        display: flex;
        justify-content: space-between;
        font-size: 19px;
        font-weight: 500;
        margin-bottom: 15px;
        span {
          text-transform: capitalize;
        }
      }
      .lesson {
        text-align: left;
        display: flex;
        font-size: 17px;
        padding-bottom: 10px;
        margin-bottom: 12px;
        border-bottom: 1px solid #e9e9e9;
        &:last-child {
          border-bottom: none;
          padding-bottom: 2px;
        }
        .start {
          padding-left: 10px;
          width: 66px;
          font-weight: 300;
          color: $gray;
        }
        .subject {
          font-weight: 500;
          margin-bottom: 5px;
        }
        .lecturer {
          font-weight: 500;
          color: $dark-blue;
          margin-right: 14px;
        }
      }
    }
  }
  .pagination {
    cursor: pointer;
    border-radius: 2px;
    border: 2px solid $light-blue;
    background-color: $light-blue;
    color: $white;
    font-weight: 500;
    padding: 0.5em 1.2em;
    transition: all 0.2s ease;
    &:hover {
      transition: all 0.2s ease;
      background-color: inherit;
      color: $light-blue;
    }
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 1325px) {
  .students {
    padding: 0 30px;
  }
}

@media screen and (max-width: 1250px) {
  .students {
    padding: 0 5px;
    .filter {
      flex-direction: column;
      align-items: center;
      .radios {
        margin-bottom: 10px;
      }
      select {
        background-color: inherit;
        width: 100%;
        margin-bottom: 10px;
        padding: 2px 0;
      }
      #specialty {
        width: 100%;
      }
      input[type="search"] {
        background-color: inherit;
        width: 100%;
        padding: 5px;
        &::placeholder {
          text-align: left;
        }
      }
    }
    .schedule {
      .day {
        .header {
          font-size: 1.05em;
        }
        .lesson {
          font-size: 0.95em;
          .start {
            width: 55px;
          }
          .subjectAndLecturer {
            flex: 1;
          }
          .lecturer {
            margin-right: 0;
          }
          span {
            display: block;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
}
