@import "../../colors.scss";

.footer {
  flex: 0 0 auto;
  display: grid;
  border-top: 1px solid #e6e6e6;
  padding: 25px 0 25px 85px;
  grid-template-columns: 21% 79%;
  .addr {
    font-weight: 500;
    span {
      display: block;
      &:nth-child(2) {
        margin-bottom: 5px;
      }
    }
  }
  .center {
    width: 945px;
    // display: flex;
    // align-items: center;
    // justify-content: space-between;
    // flex-direction: column;
    .nav {
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      div {
        width: 180px;
        margin-right: 10px;
        &:first-child,
        &:nth-child(2) {
          width: 110px;
        }
        h4 {
          font-size: 0.94em;
          margin-bottom: 4px;
        }
        a {
          display: block;
          text-decoration: none;
          color: $black;
          font-size: 0.88em;
          margin-bottom: 3px;
          line-height: 19px;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
    span {
      font-size: 0.76em;
    }
  }
  .flex {
    display: flex;
  }
  .link {
    text-decoration: none;
    color: $light-blue;
    &:hover {
      text-decoration: underline;
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    padding: 25px 0 25px 55px;
    .center {
      width: 820px;
    }
  }
}

@media screen and (max-width: 1250px) {
  .footer {
    display: none;
  }
}
