@import "../../colors.scss";

.nav {
  position: fixed;
  margin-left: 85px;
  a {
    display: block;
    text-decoration: none;
    color: $black;
    margin-bottom: 15px;
    font-size: 1.52em;
    transition: all 0.1s ease;
    background: none !important;
    &:hover {
      transition: all 0.1s ease;
      color: $dark-blue;
    }
  }
}

@media screen and (max-width: 1400px) {
  .nav {
    margin-left: 55px;
  }
}

@media screen and (max-width: 1250px) {
  .nav {
    display: none;
  }
}
