.students {
  text-align: center;
  padding: 0 50px;
}
.students .filter {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  animation: fadein 0.6s;
}
.students .filter .radios .radio {
  margin-right: 10px;
}
.students .filter .radios .radio label {
  margin-right: 3px;
}
.students .filter select {
  border: none;
  border-bottom: 1px solid #141414;
  font-size: 1em;
  background-color: inherit;
}
.students .filter #specialty {
  width: 210px;
}
.students .filter input[type=search] {
  border: none;
  background-color: inherit;
  border-bottom: 1px solid #141414;
  font-size: 1em;
  width: 197px;
  padding: 0 5px;
}
.students .filter input[type=search]::placeholder {
  color: #141414;
  text-align: center;
}
.students .schedule .day {
  margin-bottom: 20px;
  animation: fadein 0.8s;
}
.students .schedule .day .header {
  display: flex;
  justify-content: space-between;
  font-size: 19px;
  font-weight: 500;
  margin-bottom: 15px;
}
.students .schedule .day .header span {
  text-transform: capitalize;
}
.students .schedule .day .lesson {
  text-align: left;
  display: flex;
  font-size: 17px;
  padding-bottom: 10px;
  margin-bottom: 12px;
  border-bottom: 1px solid #e9e9e9;
}
.students .schedule .day .lesson:last-child {
  border-bottom: none;
  padding-bottom: 2px;
}
.students .schedule .day .lesson .start {
  padding-left: 10px;
  width: 66px;
  font-weight: 300;
  color: #C4C4C4;
}
.students .schedule .day .lesson .subject {
  font-weight: 500;
  margin-bottom: 5px;
}
.students .schedule .day .lesson .lecturer {
  font-weight: 500;
  color: #5581F1;
  margin-right: 14px;
}
.students .pagination {
  cursor: pointer;
  border-radius: 2px;
  border: 2px solid #00C0FF;
  background-color: #00C0FF;
  color: #FEFEFE;
  font-weight: 500;
  padding: 0.5em 1.2em;
  transition: all 0.2s ease;
}
.students .pagination:hover {
  transition: all 0.2s ease;
  background-color: inherit;
  color: #00C0FF;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media screen and (max-width: 1325px) {
  .students {
    padding: 0 30px;
  }
}
@media screen and (max-width: 1250px) {
  .students {
    padding: 0 5px;
  }
  .students .filter {
    flex-direction: column;
    align-items: center;
  }
  .students .filter .radios {
    margin-bottom: 10px;
  }
  .students .filter select {
    background-color: inherit;
    width: 100%;
    margin-bottom: 10px;
    padding: 2px 0;
  }
  .students .filter #specialty {
    width: 100%;
  }
  .students .filter input[type=search] {
    background-color: inherit;
    width: 100%;
    padding: 5px;
  }
  .students .filter input[type=search]::placeholder {
    text-align: left;
  }
  .students .schedule .day .header {
    font-size: 1.05em;
  }
  .students .schedule .day .lesson {
    font-size: 0.95em;
  }
  .students .schedule .day .lesson .start {
    width: 55px;
  }
  .students .schedule .day .lesson .subjectAndLecturer {
    flex: 1;
  }
  .students .schedule .day .lesson .lecturer {
    margin-right: 0;
  }
  .students .schedule .day .lesson span {
    display: block;
    margin-bottom: 2px;
  }
}