.footer {
  flex: 0 0 auto;
  display: grid;
  border-top: 1px solid #e6e6e6;
  padding: 25px 0 25px 85px;
  grid-template-columns: 21% 79%;
}
.footer .addr {
  font-weight: 500;
}
.footer .addr span {
  display: block;
}
.footer .addr span:nth-child(2) {
  margin-bottom: 5px;
}
.footer .center {
  width: 945px;
}
.footer .center .nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.footer .center .nav div {
  width: 180px;
  margin-right: 10px;
}
.footer .center .nav div:first-child, .footer .center .nav div:nth-child(2) {
  width: 110px;
}
.footer .center .nav div h4 {
  font-size: 0.94em;
  margin-bottom: 4px;
}
.footer .center .nav div a {
  display: block;
  text-decoration: none;
  color: #141414;
  font-size: 0.88em;
  margin-bottom: 3px;
  line-height: 19px;
}
.footer .center .nav div a:hover {
  text-decoration: underline;
}
.footer .center span {
  font-size: 0.76em;
}
.footer .flex {
  display: flex;
}
.footer .link {
  text-decoration: none;
  color: #00C0FF;
}
.footer .link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1400px) {
  .footer {
    padding: 25px 0 25px 55px;
  }
  .footer .center {
    width: 820px;
  }
}
@media screen and (max-width: 1250px) {
  .footer {
    display: none;
  }
}