@font-face {
  font-family: "Geometria";
  src: url("/static/fonts/Geometria.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "Geometria";
  src: url("/static/fonts/Geometria-Bold.woff") format("woff");
  font-weight: bold;
}
@font-face {
  font-family: "Geometria";
  src: url("/static/fonts/Geometria-Heavy.woff") format("woff");
  font-weight: 900;
}
@font-face {
  font-family: "Geometria";
  src: url("/static/fonts/Geometria-Medium.woff") format("woff");
  font-weight: 500;
}
* {
  font-family: "Geometria", sans-serif;
  margin: 0;
  padding: 0;
}

body {
  font-size: 17px;
}

html,
body,
.alignContainer {
  height: 100vh;
}

body {
  background-color: #FEFEFE;
}

.alignContainer {
  margin-top: 100px;
  padding: 0 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.layoutContainer {
  flex: 1 0 auto;
  margin-top: 44px;
}
.layoutContainer .content {
  margin: 0 auto;
  width: 870px;
  margin-bottom: 50px;
}

@media screen and (max-width: 1500px) {
  .alignContainer {
    padding: 0px;
  }
}
@media screen and (max-width: 1325px) {
  body {
    font-size: 16px;
  }

  .layoutContainer .content {
    width: 800px;
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 1250px) {
  .alignContainer {
    margin-top: 50px;
    padding: 0 50px;
  }

  .layoutContainer {
    height: 100%;
    margin-top: 22px;
  }
  .layoutContainer .content {
    width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .alignContainer {
    padding: 0 10px;
  }
}