.nav {
  position: fixed;
  margin-left: 85px;
}
.nav a {
  display: block;
  text-decoration: none;
  color: #141414;
  margin-bottom: 15px;
  font-size: 1.52em;
  transition: all 0.1s ease;
  background: none !important;
}
.nav a:hover {
  transition: all 0.1s ease;
  color: #5581F1;
}

@media screen and (max-width: 1400px) {
  .nav {
    margin-left: 55px;
  }
}
@media screen and (max-width: 1250px) {
  .nav {
    display: none;
  }
}