@import "../../colors.scss";

.mobNav {
  z-index: 999;
  position: fixed;
  left: 0;
  bottom: 0;
  display: none;
  justify-content: space-between;
  width: 100%;
  background-color: #f1f1f1;
  a {
    text-align: center;
    width: 100%;
    text-decoration: none;
    color: $black;
    padding: 10px 0;
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 1250px) {
  .mobNav {
    display: flex;
  }
}
