.lecturer {
  margin-bottom: 15px;
  padding: 0 50px;
}
.lecturer .filter {
  display: flex;
  margin-bottom: 25px;
  animation: fadein 0.6s;
}
.lecturer .filter select {
  border: none;
  border-bottom: 1px solid #141414;
  font-size: 1em;
  background-color: inherit;
  margin-right: 16px;
}
.lecturer .filter .department {
  width: 235px;
}
.lecturer .filter input[type=search] {
  border: none;
  background-color: inherit;
  border-bottom: 1px solid #141414;
  font-size: 1em;
  width: 185px;
  padding: 1px 5px;
}
.lecturer .filter input[type=search]::placeholder {
  color: #141414;
}

.day {
  margin-bottom: 20px;
  animation: fadein 0.8s;
}
.day .header {
  display: flex;
  justify-content: space-between;
  font-size: 19px;
  font-weight: 500;
  margin-bottom: 10px;
}
.day .header .weekday {
  text-transform: capitalize;
}
.day .lecturer {
  padding: 0;
  text-align: left;
  font-weight: 500;
  margin-bottom: 15px;
  color: #5581F1;
}
.day .lesson {
  text-align: left;
  display: flex;
  font-size: 17px;
  padding-bottom: 10px;
  margin-bottom: 12px;
  border-bottom: 1px solid #e9e9e9;
}
.day .lesson:last-child {
  border-bottom: none;
  padding-bottom: 2px;
}
.day .lesson .start {
  padding-left: 10px;
  width: 26px;
  font-weight: 400;
  color: #b7b7b7;
}
.day .lesson .groupAndAudience {
  text-align: left;
}
.day .lesson .groupAndAudience .group {
  margin-right: 5px;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media screen and (max-width: 1250px) {
  .lecturer {
    padding: 0 5px;
  }
  .lecturer .filter {
    flex-direction: column;
    align-items: center;
  }
  .lecturer .filter select {
    background-color: inherit;
    width: 100%;
    margin: 0;
    margin-bottom: 10px;
    padding: 2px 0;
  }
  .lecturer .filter .department {
    width: 100%;
  }
  .lecturer .filter input[type=search] {
    background-color: inherit;
    width: 100%;
    padding: 5px;
  }
  .lecturer .filter input[type=search]::placeholder {
    text-align: left;
  }
  .lecturer .day .header {
    font-size: 1.05em;
  }
  .lecturer .day .header span:nth-child(2) {
    font-size: 0.95em;
  }
  .lecturer .day .lesson .groupAndAudience {
    font-size: 0.95em;
  }
  .lecturer .day .lesson .groupAndAudience .group:first-child {
    display: block;
    margin-bottom: 2px;
  }
}